<template>
  <p class="blank"></p>
  <div class="container">
    <div class="row">
      <div class="col-xs-8">
        <h2>{{msg.title}}</h2>
        <p class="text-muted text-left">
          <span>
            猫眼电影
          </span>
          <span>{{msg.publishDate}}</span>
          <span>
            <i class="glyphicon glyphicon-eye-open"></i>
            <span class="">
              {{msg.readNum}}
            </span>
          </span>
        </p>
        <div class="img">
          <img :src="this.url + msg.cover" alt="">
        </div>
        <h4 v-html="content"></h4>
        <div class="row text-right">
          <h5>
            <span class="like" @click="like()">
              <i class="glyphicon glyphicon-thumbs-up"></i>{{msg.likeNum}}
            </span>
          </h5>
        </div>
        
        <div class="row">
          <div class="" v-for='(i,index) in news' :key="index">
          <a href="">
            {{i.content}}
          </a>
        </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props:['id'],
  data(){
    return{
      msg:[],
      news:[],
      url:'http://124.93.196.45:10001',
      content:'',
    }
  },
  methods:{
    like(){
      this.axios({
        method:"put",
        url:"/prod-api/api/movie/press/press/like/"+this.id,
        headers:{
          Authorization:window.sessionStorage["Authorization"],
        },
      })
      .then((resopnse)=>{
        if(resopnse.status == 200){
          if(resopnse.data.code == 200){
            console.log(resopnse.data)
            alert("点赞成功");
            // this.$router.go(0);
          }else{
            alert("请先登录");
            console.log(resopnse.data)
          }
        }else{
          console.log(resopnse.data)
        }
      })
      .catch((error)=>{
        console.log(error)
      })
    },
  },
  beforeUpdate(){
    this.axios({
      method:"get",
      url:"/prod-api/api/movie/press/press/"+this.id,
    })
    .then((resopnse)=>{
      if(resopnse.status == 200){
        if(resopnse.data.code == 200){
          console.log(resopnse.data)
          this.msg = resopnse.data.data
          this.content = this.msg.content
          // console.log(this.msg.content)
        }else{
          console.log(this.id)
          
          console.log(resopnse.data)
        }
      }else{
        console.log(resopnse.data)
      }
    })
    .catch((error)=>{
      console.log(error)
    })
  },
  created(){
    this.axios({
      method:"get",
      url:"/prod-api/api/movie/press/press/"+this.id,
    })
    .then((resopnse)=>{
      if(resopnse.status == 200){
        if(resopnse.data.code == 200){
          console.log(resopnse.data)
          this.msg = resopnse.data.data
          this.content = this.msg.content
          // console.log(this.msg.content)
        }else{
          console.log(this.id)
          
          console.log(resopnse.data)
        }
      }else{
        console.log(resopnse.data)
      }
    })
    .catch((error)=>{
      console.log(error)
    })
  }
  
}
</script>

<style scoped>
p.text-muted span{
  margin-right: 10px;
}
span.like{
  border-radius: 20px;
  padding: 6px 10px;
  border: 1px solid black;
}
span.like:hover{
  color: rgb(201, 21, 21);
  border: 1px solid rgb(201, 21, 21);
}
.img img{
  width: 100%;
}
.container{
  background: #fff;
  margin-bottom: 40px;
}
</style>